import { supabase } from "@/utils/supabase/alSupabase";
import { error } from "console";

export async function getInboxMessages(userId: string){
    const {data, error} = await supabase
        .from("messages_safe")
        .select(`
            id,
            body,
            created_at,
            is_anon,
            from_user,
            from_public_id,
            from_username
            `)
        .eq("to_user", userId)
        .order("created_at", {ascending: true})
    if(error) throw error;
    return data || []
}

export async function getCurrentMessage(messageId: string, userId: string) {
    const {data, error} = await supabase
    .from("messages_safe")
    .select("*")
    .eq("id", messageId)
    .eq("to_user", userId)
    .single()
    if (error) throw error
    return data || []
}

export async function getSentMessages(userId: string) {
    const {data, error} = await supabase
        .from("messages")
        .select(`
            id,
            body,
            created_at,
            is_anon,
            to_user: to_user (
                public_id,
                username
            )
        `)
        .eq("from_user", userId)
        .order("created_at", {ascending: true})
    if(error) throw error
    return data || []
}

export async function sendMessage({
    fromUserId,
    toPublicId,
    body,
    isAnon
}: {
    fromUserId: string,
    toPublicId: string,
    body: string,
    isAnon: boolean
}) {
    const {data: receiver, error: userError} = await supabase
        .from("users")
        .select("id_user")
        .eq("public_id", toPublicId)
        .single()
    if(userError || !receiver){
        throw new Error("–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–∏ÃÜ–¥–µ–Ω, –∫–∏—Å—å")
    }

    const {error: messageError} = await supabase
        .from("messages")
        .insert({
            from_user: fromUserId,
            to_user: receiver.id_user,
            body,
            is_anon: isAnon
        })
    if(messageError) throw messageError
}